import React, { Component } from 'react'

export default class EditableList extends Component {
  static defaultProps = {
    className: '',
    defaultValue: '',
    options: [],
  }

  state = {
    selected: this.props.defaultValue,
  }

  render = () => {
    const { className, options } = this.props
    const { selected } = this.state

    return (
      <select className={`editable-list ${className}`}>
        {options.map(option => (
          <option key={option} value={option} selected={option === selected}>
            {option}
          </option>
        ))}
      </select>
    )
  }
}
